import { cn } from "@/lib/utils";
import React from "react";

export interface NotificationItemProps {
    name: string;
    description: string;
    icon: React.ReactNode;
    color: string;
    time: string;
}

export const NotificationItem = ({ name, description, icon, color, time }: NotificationItemProps) => {
    return (
        <figure
            className={cn(
                "relative mx-auto min-h-fit w-full max-w-[500px] cursor-pointer overflow-hidden rounded-3xl p-6",
                "bg-black/50 backdrop-blur-md"
            )}
        >
            <div className="flex flex-row items-center gap-5">
                <div
                    className="flex size-14 items-center justify-center rounded-2xl flex-shrink-0"
                    style={{
                        backgroundColor: color,
                    }}
                >
                    <span className="text-2xl">{icon}</span>
                </div>
                <div className="flex flex-col overflow-hidden">
                    <figcaption className="flex flex-row items-center text-xl font-medium whitespace-pre text-black dark:text-white">
                        <span className="text-lg sm:text-xl">{name}</span>
                        <span className="mx-2"></span>
                        <span className="text-sm text-gray-500">{time}</span>
                    </figcaption>
                    <p className="text-base font-normal text-black/60 dark:text-white/60 leading-snug">
                        {description}
                    </p>
                </div>
            </div>
        </figure>
    );
};
