import { useCurrentFrame, interpolate, random } from "remotion";
import { cn } from "@/lib/utils";

interface SparkleData {
  x: string;
  y: string;
  color: string;
  delay: number;
  scale: number;
}

export const SparklesText = ({
  children,
  colors = { first: "#9E7AFF", second: "#FE8BBB" },
  className,
  sparklesCount = 10,
}: {
  children: React.ReactNode;
  colors?: { first: string; second: string };
  className?: string;
  sparklesCount?: number;
}) => {
  const frame = useCurrentFrame();

  // Generate deterministic sparkles using seeded random
  const sparkles: SparkleData[] = Array.from({ length: sparklesCount }, (_, i) => ({
    x: `${random(`sparkle-x-${i}`) * 100}%`,
    y: `${random(`sparkle-y-${i}`) * 100}%`,
    color: random(`sparkle-color-${i}`) > 0.5 ? colors.first : colors.second,
    delay: random(`sparkle-delay-${i}`) * 60,
    scale: random(`sparkle-scale-${i}`) * 1 + 0.3,
  }));

  return (
    <div className={cn("text-6xl font-bold", className)}>
      <span className="relative inline-block">
        {sparkles.map((sparkle, i) => {
          const sparkleFrame = (frame + sparkle.delay) % 24;
          const opacity = interpolate(
            sparkleFrame,
            [0, 8, 16, 24],
            [0, 1, 1, 0],
            { extrapolateLeft: "clamp", extrapolateRight: "clamp" }
          );
          const scale = interpolate(
            sparkleFrame,
            [0, 12, 24],
            [0, sparkle.scale, 0],
            { extrapolateLeft: "clamp", extrapolateRight: "clamp" }
          );
          const rotate = interpolate(
            sparkleFrame,
            [0, 24],
            [75, 150]
          );

          return (
            <svg
              key={i}
              className="pointer-events-none absolute z-20"
              style={{
                left: sparkle.x,
                top: sparkle.y,
                opacity,
                transform: `scale(${scale}) rotate(${rotate}deg)`,
              }}
              width="21"
              height="21"
              viewBox="0 0 21 21"
            >
              <path
                d="M9.82531 0.843845C10.0553 0.215178 10.9446 0.215178 11.1746 0.843845L11.8618 2.72026C12.4006 4.19229 12.3916 6.39157 13.5 7.5C14.6084 8.60843 16.8077 8.59935 18.2797 9.13822L20.1561 9.82534C20.7858 10.0553 20.7858 10.9447 20.1561 11.1747L18.2797 11.8618C16.8077 12.4007 14.6084 12.3916 13.5 13.5C12.3916 14.6084 12.4006 16.8077 11.8618 18.2798L11.1746 20.1562C10.9446 20.7858 10.0553 20.7858 9.82531 20.1562L9.13819 18.2798C8.59932 16.8077 8.60843 14.6084 7.5 13.5C6.39157 12.3916 4.19225 12.4007 2.72023 11.8618L0.843814 11.1747C0.215148 10.9447 0.215148 10.0553 0.843814 9.82534L2.72023 9.13822C4.19225 8.59935 6.39157 8.60843 7.5 7.5C8.60843 6.39157 8.59932 4.19229 9.13819 2.72026L9.82531 0.843845Z"
                fill={sparkle.color}
              />
            </svg>
          );
        })}
        <strong>{children}</strong>
      </span>
    </div>
  );
};
